*,
*:after,
*:before {
	box-sizing: border-box;
}

body,
html {
	height: 100%;
    padding: 0;
    margin: 0;
}

body {
	-webkit-text-size-adjust: 100%;
	background-color: $color-secondary;
    color: #2b2b2b;
	display: flex;
	flex-direction: column;
    font: 16px/1.5 Arial, sans-serif;
}

button,
input {
    font: inherit;
}
