@keyframes spring {
  0%   { opacity: .25; transform: translateY(5px); }
  100% { opacity: 1; transform: translateY(-5px);}
}

.chat-messages {
    flex-grow: 1;
    height: 33vh;
    position: relative;

    &__typing-indicator {
        bottom: 0;
        color: #fff;
        font-style: italic;
        left: 1em;
        position: absolute;
    }

    &__elipsis {
        color: #fff;
        margin-left: .25em;
        position: relative;
        & > span {
            animation: spring;
            animation-direction: alternate;
            animation-duration: 1s;
            animation-iteration-count: infinite;
            background-color: #fff;
            border-radius: 50%;
            display: inline-block;
            height: 6px;
            width: 6px;

            &:nth-child(2) {
                animation-delay: 100ms;
            }

            &:nth-child(3) {
                animation-delay: 300ms;
            }

            & + span {
                margin-left: .125em;
            }
        }
    }

    &__log {
        list-style: none;
        margin: 0 0 1.5em;
        max-height: 100%;
        overflow: scroll;
        padding: 1em 1em 1.5em;
    }

    &__bubble {
        border-radius: 1em;
        background-color: #e0e0e0;
        display: inline-block;
        max-width: 80%;
        padding: .5em 1em;
        // position: relative;

        &:before {
            border-color: transparent #e0e0e0 transparent transparent;
            border-style: solid;
            border-width: 10px 10px 10px 0;
            content: '';
            height: 0;
            left: -9px;
            position: absolute;
            top: 10px;
            width: 0;
        }
    }

    &__message {
        border: 1em;
        color: #000;
        position: relative;

        & > div {
        }

        &[data-incoming="false"] {
            text-align: right;
            & > div {
                background-color: $color-primary;
                color: #000;
                left: 20%;

                &:before {
                    border-color: transparent transparent transparent $color-primary;
                    border-width: 10px 0 10px 10px;
                    left: initial;
                    right: -9px;
                }
            }
        }

        & + .chat-messages__message {
            margin-top: .5em;
        }
    }
}
