.chat {
    &__wrapper {
        background-color: darken($color-secondary, 8%);
        max-width: 500px;
        padding: 1em;
    }

    &__log {
        border: 1px solid #999;
        border-radius: .25em;
        height: 33vh;
        list-style: none;
        padding: .5em .5em 2em;
        position:relative;
        overflow: scroll;
    }


    &__typing-indicator {
        bottom: 0;
        font-size: .9em;
        font-style: italic;
        position: absolute;
    }

    &__input {
        position: relative;

        &__send-button {
            background-color: $color-primary;
            border: none;
            border-radius: 0 1em 1em 0;
            bottom: 0;
            color: #fff;
            position: absolute;
            right: 0;
            top: 0;
            width: 5em;

            &[disabled] {
                background-color: #e0e0e0;
                color: #666;
            }
        }

        &__field {
            border: none;
            border-radius: 1rem;
            outline: none;
            padding: .25rem 5rem .25rem .5rem;
            width: 100%;
        }
    }
}
